/*******************************
            Theme
*******************************/

@type    : 'element';
@element : 'list';

@import (multiple) '../../theme.config';

/*******************************
            List
*******************************/

.unstyled {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
}

ol.ui.list {
  .unstyled;
  counter-reset: counter;

  > li {
    position: relative;
    padding-top: 8px;
    padding-bottom: 9px;
    padding-left: 30px;

    &:before {
      content: counter(counter);
      counter-increment: counter;
      display: inline-block;
      width: 20px;
      height: 20px;
      position: absolute;
      top: 10px;
      left: 0;
      color: @listNumberColor;
      font-size: @mini;
      font-weight: bold;
      line-height: 20px;
      text-align: center;
      background-color: @listNumberBackground;
      border-radius: 50%;
    }
  }
}

/*******************************
        CHALLANGE LIST
*******************************/

.ui.challenge.list {
  .unstyled;
  overflow: hidden;
  margin: 0 auto 20px;
  max-width: 320px;
}

.challenge-item {
  width: 50%;
  float: left;
  text-align: center;
  padding: 10px;

  &:nth-child(1) {
    border-bottom: 1px solid @borderColor;
  }

  &:nth-child(2) {
    border-bottom: 1px solid @borderColor;
    border-left: 1px solid @borderColor;
  }

  &:nth-child(4) {
    border-left: 1px solid @borderColor;
  }
}

.challenge-title {
  display: block;
  text-transform: uppercase;
  font-family: @pageMediumFont;
  letter-spacing: .05em;
}

.challenge-point {
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  border-radius: 50%;
  background-color: @challengePointBackground;
}

.challenge-points-number {
  font-family: @pageBoldFont;
  letter-spacing: .05em;
  color: @challengePointsColor;
  font-size: 30px;
  line-height: 1;
}

.challenge-points-text {
  letter-spacing: .05em;
  font-family: @pageMediumFont;
  font-size: @tiny;
}


/*******************************
        KEY-VALUE LIST
*******************************/

.key-value {
  .unstyled;
  margin-top: -5px;

  > li {
    position: relative;
    border-bottom: 1px solid @borderColor; // todo: promeni border
    margin-top: 10px;
    padding-bottom: 10px;
    padding-right: 30px;

    &:first-child {
      margin-top: 0;
    }

    &.slim.field {
      margin-top: 10px;
      margin-bottom: 0;
      padding-bottom: 0;
      padding-right: 0;
      border-bottom: 0;
    }
  }

  .key {
    color: @keyColor;
    text-transform: uppercase;
    font-size: @mini;
    font-family: @keyFontFamily;
  }

  .value {
    color: @valueColor;
    font-size: @large;

    .form-field & {
      position: absolute;
    }
  }

  .action {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}

.accounts {
  margin-top: -10px;

  li {
    line-height: 33px;
  }

  a {
    display: block;
    color: @textColor;
    font-size: @large;
  }

  .icon {
    width: 20px;
    height: 20px;
    margin-right: 10px;
    position: relative;
    top: -2px;
    vertical-align: middle;
  }
}

// Timeline with checkboxes

.ui.timeline {
  position: relative;
  .unstyled;
  font-size: @large;

  // Line
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 9px;
    display: block;
    height: 100%;
    width: 1px;
    border-right: 2px solid @borderColor;
  }

  // Bottom line fade
  &:after {
    content: "";
    display: block;
    position: absolute;
    bottom: -5px;
    left: 10px;
    height: 20px;
    width: 2px;
    #gradient > .vertical(transparent, @pageBackground);
  }

  // Items
  > li {
    position: relative;
    padding-left: 30px;
    padding-bottom: 20px;
  }
}

.timeline-point {
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  background-color: @timelinePointBackground;
  border-radius: 50%;
  text-align: center;

  .icon {
    width: 13px;
    height: 13px;
    fill: @white !important;
    position: relative;
    top: -3px;
  }
}

.timeline-meta {
  color: @colorQuiet;
  font-size: @tiny;
  font-family: @pageMediumFont;
}

.timeline-share {
  display: inline-block;
  text-transform: uppercase;
  font-size: @tiny;
  font-family: @pageMediumFont;

  .icon {
    width: 16px;
    height: 16px;
    vertical-align: middle;
    margin-right: 5px;
    position: relative;
    top: -1px;
  }
}

.timeline-share-twitter {
  color: @twitterColor;
  margin-right: 15px;

  &:hover,
  &:focus {
    color: @twitterColor;
  }
}

.timeline-share-facebook {
  color: @facebookColor;

  &:hover,
  &:focus {
    color: @facebookColor;
  }
}

