/*******************************
            Theme
*******************************/

@type    : 'element';
@element : 'divider';

@import (multiple) '../../theme.config';


/*******************************
            Divider
*******************************/

.ui.divider {
  margin: @margin;

  line-height: 1;
  height: 0em;

  font-weight: @fontWeight;
  letter-spacing: @letterSpacing;
  color: @color;
  font-size: 0.9em; // 13px

  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

