/**************************************
    SPRAWSM MISC STYLES AND HELPERS
**************************************/

/*******************************
            Theme
*******************************/

@type    : 'element';
@element : 'sprawsm';

@import (multiple) '../../theme.config';


/*******************************
        BACKGROUNDS
*******************************/
.fixed.cover {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.login {
  background: url("../images/child.jpg") no-repeat center center;
  background-size: cover;
}

.faded {
  &:before {
    content: "";
    display: block;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 30vh;
    z-index: 100;
    #gradient > .vertical(transparent, #000);
  }
}

/*******************************
        HELPERS
*******************************/
.light.text {
  color: #fff;
}

.quiet.text {
  color: @colorQuiet !important;
}

.bleached.text {
  color: @colorBleached;
}

.delete.text {
  color: #e94d4d;
}

.overflowed.text {
  max-width: 100%;
  display: block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden
}

.middle.top.offset {
  margin-top: @spacingMiddle !important;
}

.middle.bottom.offset {
  margin-bottom: @spacingMiddle !important;
}

.large.bottom.offset {
  margin-bottom: @spacingLarge !important;
}

.small.top.offset {
  margin-top: @spacingSmall !important;
}

.small.bottom.offset {
  margin-bottom: @spacingSmall !important;
}

.large.text {
  font-size: @emSize * @largeSize;
}

.small.text {
  font-size: @emSize * @smallSize;
}

.big.text {
  font-size: 30px;
  line-height: 1;
}

.strong {
  font-weight: bold;
}

.primary.text {
  color: @linkColor;
}

.right.floated {
  float: right;
}

.clearfix {
  &:before,
  &:after {
    content: "";
    display: table;
  }
  &:after {
    clear: both;
  }
}

.center.aligned {
  text-align: center;
}

.delete-link {
  display: inline-block;
  color: @colorDelete;
  fill: @colorDelete;
  font-size: @emSize * @largeSize;

  &:hover,
  &:focus {
    color: lighten(@colorDelete, 10%);
    fill: lighten(@colorDelete, 10%);
  }

  .icon {
    width: 20px;
    height: 20px;
    position: relative;
    top: 3px;
    margin-right: 2px;
  }
}

// MISC

.avatar {
  border-radius: 50%;
  vertical-align: middle;

  td & {
    margin-right: 10px;
  }
}

.share-box {
  max-width: 250px;
  margin: @spacingSmall auto 0;

  .button {
    margin-bottom: @spacingSmall;
  }
}


// Table columns
.rank {
  width: 45px;
  padding-right: 0 !important;
  text-align: center !important;
}

.points {
  width: 90px;
  padding-left: 0 !important;
  text-align: right !important;
}

.user {
  display: table;
  width: 100%;
  table-layout: fixed;
}

.user-avatar,
.user-name {
  display: table-cell;
  vertical-align: middle;
}

.user-avatar {
  width: 40px;
}

.loadMixins();
