/*!
 * # Sprawsm - Sidebar
 *
 */

/*******************************
            Theme
*******************************/

@type    : 'element';
@element : 'sidebar';

@import (multiple) '../../theme.config';

/*******************************
            SIDEBAR
*******************************/

.ui.sidebar {
  width: 100%;
  max-width: @sidebarMaxWidth;
  height: 100% !important;
  padding-top: 30px;
  padding-bottom: 20px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  color: #fff;
  text-align: center;
  background: rgba(87,140,238,.25) url("../images/child-blended.jpg") no-repeat center center;
  background-size: cover;
  transform: translate3d(-100%, 0, 0);
  transition: transform @sidebarAnimationSpeed;
  backface-visibility: hidden;
  will-change: transform;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 1000;

  &.visible {
    transform: translate3d(0, 0, 0);
  }

  @media (min-width: @sidebarVisibilityBreakpoint) {
    transform: translate3d(0, 0, 0);
  }

  .toggle-sidebar {
    position: absolute;
    top: 10px;
    left: 15px;

    @media (min-width: @sidebarVisibilityBreakpoint) {
      display: none;
    }
  }
}

.sidebar-header {
  margin-bottom: 25px;
}

.sidebar-title {
  margin-top: 15px;
  color: rgba(255,255,255,.6);
  font-family: @pageFont;
  font-weight: bold;
}

.ui.sidebar-menu {
  list-style: none;
  padding-left: 0;

  li {
    border-bottom: 1px solid rgba(247,247,247,.1);

    &:first-child {
      border-top: 1px solid rgba(247,247,247,.1);
    }
  }

  a {
    display: block;
    padding-top: 15px;
    padding-bottom: 15px;
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    font-family: @pageBoldFont;
    letter-spacing: .1em;

    &:hover,
    &:focus {
      background-color: rgba(255,255,255,.1);
    }
  }
}

.sidebar-cta {
  margin-top: 35px;
  margin-right: 10px;
  margin-left: 10px;
}

.loadUIOverrides();
