/*!
 * # Sprawsm - Carousel
 *
 */


/*******************************
            Theme
*******************************/

@type    : 'element';
@element : 'carousel';

@import (multiple) '../../theme.config';


/*******************************
            Carousel
*******************************/

// Slider
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
   -khtml-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -ms-touch-action: pan-y;
      touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;

  &:focus {
    outline: none;
  }

  &.dragging {
    cursor: pointer;
    cursor: hand;
  }
}

.slick-track,
.slick-list {
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;

  &:before,
  &:after {
    content: "";
    display: table;
  }

  &:after {
    clear: both;
  }

  .slick-loading & {
    visibility: hidden;
  }
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;

  [dir="rtl"] & {
    float: right;
  }

  &.slick-loading img {
    display: none;
  }

  display: none;

  &.dragging img {
    pointer-events: none;
  }

  .slick-initialized & {
    display: block;
  }

  .slick-loading & {
    visibility: hidden;
  }

  .slick-vertical & {
    display: block;
    height: auto;
    border: 1px solid transparent;
  }
}

.slick-arrow.slick-hidden {
  display: none;
}

// Dots
.slick-dotted.slick-slider {
  margin-bottom: 66px;
}

.slick-dots {
  position: absolute;
  bottom: -40px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;

  > li {
    position: relative;
    display: inline-block;
    height: @slickDotSize;
    width: @slickDotSize;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;

    > button {
      display: block;
      padding: 0;
      width: @slickDotSize;
      height: @slickDotSize;
      border: 0;
      background-color: @slickDotColor;
      line-height: 0px;
      font-size: 0px;
      border-radius: 50%;
      outline: none;
      color: transparent;
      cursor: pointer;

      &:hover,
      &:focus {
        outline: none;
      }

      &:before {
        position: absolute;
        top: 0;
        left: 0;
        content: "";
        width: 20px;
        height: 20px;
        color: @slickDotColor;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
      }
    }

    &.slick-active button {
      background-color: @slickDotActiveColor;
    }
  }
}
