/*!
 * # Sprawsm - Card
 *
 */


/*******************************
            Theme
*******************************/

@type    : 'element';
@element : 'card';

@import (multiple) '../../theme.config';


/*******************************
             Card
*******************************/

.card {
  margin-left: 1px;
  margin-right: 1px;
  text-align: center;
  border: 1px solid @cardBorderColor;
  border-radius: @defaultBorderRadius;
  transform: scale(0.95);
  background-color: @cardBackgroundColor;

  .slick-current & {
    transform: scale(1);
  }

  @media (min-width: @containerBreakpoint) {
    background-color: @cardBackgroundForLargerScreens;
  }
}

.card-header {
  padding-top: @spacingMiddle;
  padding-bottom: 10px;
  font-family: @pageMediumFont;

  .icon {
    width: 35px;
    height: 35px;
  }
}

// Leaderboard
.leaderboard-footer {
  padding-top: @spacingMiddle;
  padding-bottom: @spacingMiddle;
  text-align: center;
}

.leaderboard-wrapper {
  position: relative;

  // Shades on top and bottom
  &:before,
  &:after {
    content: "";
    display: block;
    position: absolute;
    height: 50px;
    left: 0;
    right: 0;
  }

  &:before {
    top: 0;
    #gradient > .vertical(@pageBackground, @colorTransparent);

    // Matching container's background on larger screens
    @media (min-width: @containerBreakpoint) {
      #gradient > .vertical(@white, @colorTransparent);
    }
  }

  &:after {
    bottom: 0;
    #gradient > .vertical(@colorTransparent, @pageBackground);

    // Matching container's background on larger screens
    @media (min-width: @containerBreakpoint) {
      #gradient > .vertical(@colorTransparent, @white);
    }
  }

  // Shades when in card box
  .card & {
    &:before {
      #gradient > .vertical(@cardBackgroundColor, @colorTransparent);

      // Matching container's background on larger screens
      @media (min-width: @containerBreakpoint) {
        #gradient > .vertical(@cardBackgroundForLargerScreens, @colorTransparent);
      }
    }

    &:after {
      #gradient > .vertical(@colorTransparent, @cardBackgroundColor);

      // Matching container's background on larger screens
      @media (min-width: @containerBreakpoint) {
        #gradient > .vertical(@colorTransparent, @cardBackgroundForLargerScreens);
      }
    }
  }
}

.loadUIOverrides();
.loadMixins();
