/*!
 * # Sprawsm - Icons
 *
 */


/*******************************
            Theme
*******************************/

@type    : 'element';
@element : 'icon';

@import (multiple) '../../theme.config';


/*******************************
             Icon
*******************************/

.icon {
  display: inline-block;
  width: 25px;
  height: 25px;
  fill: inherit;

  &.check {
    fill: @green;
  }

  &.edit {
    fill: @blue;
  }

  &.facebook {
    fill: @facebookColor;
  }

  &.google-plus {
    fill: @googlePlusColor;
  }

  &.twitter {
    fill: @twitterColor;
  }

  &.trophy {
    fill: @yellow;
  }

  &.chevron-right {
    fill: @white;
  }

  &.back {
    fill: @white;
  }
}

.loadUIOverrides();
