/*!
 * # Sprawsm - Section
 *
 */


/*******************************
            Theme
*******************************/

@type    : 'element';
@element : 'section';

@import (multiple) '../../theme.config';


/*******************************
             Section
*******************************/

.ui.section {

  .title {
    color: @color;
    background-color: @background;
    text-transform: uppercase;
    letter-spacing: .05em;
    font-size: @tiny;
    font-family: @titleFontFamily;
    margin-top: 0;
    margin-bottom: 0;
    padding: 10px 15px;
  }

  .subtitle {
    margin-top: 10px;
    margin-bottom: 20px;
    font-size: @large;
    text-align: center;
    font-family: @subTitleFontFamily;
  }

  .body {
    padding: 20px 15px;

    &.full {
      padding-left: 0;
      padding-right: 0;
    }
  }
}

.loadUIOverrides();
