/*******************************
             Page
*******************************/
html,
body {
  height: 100%;
}
html {
  font-size: 14px;
}
body {
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  min-width: 320px;
  background: #f9f9f9;
  font-family: 'FFDINWebPro', Arial, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 1.75;
  color: #58595b;
  font-smoothing: antialiased;
}
/*******************************
             Headers
*******************************/
h1,
h2,
h3,
h4,
h5 {
  font-family: "FFDINWebProBold", 'Helvetica Neue', Arial, Helvetica, sans-serif;
  line-height: 1.2857em;
  margin: calc(2rem -  0.14285em ) 0em 1rem;
  font-weight: bold;
  padding: 0em;
  color: #666666;
}
h1 {
  min-height: 1rem;
  font-size: 1.2875rem;
}
h2 {
  font-size: 1.1rem;
}
h3 {
  font-size: 1rem;
}
h4 {
  font-size: 0.9rem;
}
h5 {
  font-size: 1rem;
}
h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child,
h5:first-child {
  margin-top: 0em;
}
h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child {
  margin-bottom: 0em;
}
/*******************************
             Text
*******************************/
p {
  margin: 0em 0em 1em;
  line-height: 1.75;
}
p:first-child {
  margin-top: 0em;
}
p:last-child {
  margin-bottom: 0em;
}
/*-------------------
        Links
--------------------*/
a {
  color: #429ccd;
  text-decoration: none;
}
a:hover {
  color: #198ecd;
  text-decoration: none;
}
/*******************************
          Highlighting
*******************************/
/* Site */
::-webkit-selection {
  background-color: #CCE2FF;
  color: #58595b;
}
::-moz-selection {
  background-color: #CCE2FF;
  color: #58595b;
}
::selection {
  background-color: #CCE2FF;
  color: #58595b;
}
/* Form */
textarea::-webkit-selection,
input::-webkit-selection {
  background-color: rgba(100, 100, 100, 0.4);
  color: #58595b;
}
textarea::-moz-selection,
input::-moz-selection {
  background-color: rgba(100, 100, 100, 0.4);
  color: #58595b;
}
textarea::selection,
input::selection {
  background-color: rgba(100, 100, 100, 0.4);
  color: #58595b;
}
