/*******************************
            Button
*******************************/
.ui.button {
  cursor: pointer;
  display: inline-block;
  min-height: 1em;
  outline: none;
  border: none;
  vertical-align: baseline;
  background: #4fc6e0 none;
  color: #FFFFFF;
  fill: #FFFFFF;
  font-family: "FFDINWebProBold", 'Helvetica Neue', Arial, Helvetica, sans-serif;
  margin: 0em 0.25em 0em 0em;
  padding: 1.2em 1.5em 1.2em;
  text-transform: uppercase;
  text-shadow: none;
  letter-spacing: 0.05em;
  line-height: 1em;
  font-style: normal;
  text-align: center;
  text-decoration: none;
  border-radius: 0.21428571rem;
  box-shadow: 0px 0px 0px 1px transparent inset, 0px 0em 0px 0px #e0e0e0 inset;
  user-select: none;
  transition: opacity 0.1s ease, background-color 0.1s ease, color 0.1s ease, box-shadow 0.1s ease, background 0.1s ease;
  will-change: '';
