/*******************************
            Header
*******************************/
.ui.header {
  height: 250px;
  padding: 15px;
  position: relative;
  color: rgba(255, 255, 255, 0.4);
  background-color: #429ccd;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
@media (min-width: 992px) {
  .ui.header {
    height: 300px;
  }
}
.ui.header .content {
  width: 90%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 300;
}
.ui.header .title {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: .1em;
  font-size: 1.14285714rem;
  margin-top: 0;
}
.ui.header .avatar,
.ui.header .icon {
  margin-bottom: 7px;
}
.ui.header .icon {
  width: 65px;
  height: 65px;
}
.ui.header .toggle-sidebar {
  float: left;
  position: relative;
  z-index: 350;
  fill: #FFFFFF;
}
.ui.header .toggle-sidebar .icon {
  width: 25px;
  height: 25px;
}
@media (min-width: 992px) {
  .ui.header .toggle-sidebar {
    display: none;
  }
}
.ui.header .add-pin {
  float: right;
  position: relative;
  z-index: 350;
  background: rgba(255, 255, 255, 0.3);
}
@media (min-width: 992px) {
  .profile .avatar {
    width: 150px;
    height: 150px;
  }
}
.profile .title {
  margin-bottom: 0;
}
.how-to,
.profile {
  background-image: url("../images/child-blended.jpg");
}
.wasser-quiz.header {
  background-image: url("../images/child-blended.jpg");
}
.brunnen-bohren.header {
  background-image: url("../images/drill-blended.jpg");
}
.wasser-ziehen.header {
  background-image: url("../images/well-blended.jpg");
}
.der-lauf-fur-wasser.header {
  background-image: url("../images/woman-blended.jpg");
}
@media (min-width: 992px) {
  .how-to,
  .profile,
  .wasser-quiz.header,
  .brunnen-bohren.header,
  .wasser-ziehen.header,
  .der-lauf-fur-wasser.header {
    background-image: none;
  }
}
.stations .content {
  margin-top: 15px;
}
.stats {
  max-width: 420px;
  margin: 21px auto 0;
  padding-left: 0;
  list-style: none;
  font-family: "FFDINWebPro", 'Helvetica Neue', Arial, Helvetica, sans-serif;
  line-height: 1.4;
}
.stats > li {
  display: inline-block;
  width: 49.5%;
  margin-left: -4px;
  vertical-align: middle;
}
.stat {
  width: 100%;
  display: table;
  table-layout: fixed;
}
.stat-point,
.stat-text {
  display: table-cell;
  vertical-align: middle;
}
.stat-point {
  width: 55px;
}
.stat-point-circle {
  width: 45px;
  height: 45px;
  line-height: 45px;
  color: #FFFFFF;
  font-weight: bold;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
}
.stat-text {
  color: rgba(255, 255, 255, 0.5);
  text-align: left;
  font-size: 11px;
  letter-spacing: .05em;
}
@media (min-width: 380px) {
  .stat-text {
    font-size: 0.85714286rem;
  }
}
