/*******************************
            List
*******************************/
.unstyled {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
}
ol.ui.list {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  counter-reset: counter;
}
ol.ui.list > li {
  position: relative;
  padding-top: 8px;
  padding-bottom: 9px;
  padding-left: 30px;
}
ol.ui.list > li:before {
  content: counter(counter);
  counter-increment: counter;
  display: inline-block;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 10px;
  left: 0;
  color: #FFFFFF;
  font-size: 0.71428571rem;
  font-weight: bold;
  line-height: 20px;
  text-align: center;
  background-color: #8aaad9;
  border-radius: 50%;
}
/*******************************
        CHALLANGE LIST
*******************************/
.ui.challenge.list {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  overflow: hidden;
  margin: 0 auto 20px;
  max-width: 320px;
}
.challenge-item {
  width: 50%;
  float: left;
  text-align: center;
  padding: 10px;
}
.challenge-item:nth-child(1) {
  border-bottom: 1px solid #e0e0e0;
}
.challenge-item:nth-child(2) {
  border-bottom: 1px solid #e0e0e0;
  border-left: 1px solid #e0e0e0;
}
.challenge-item:nth-child(4) {
  border-left: 1px solid #e0e0e0;
}
.challenge-title {
  display: block;
  text-transform: uppercase;
  font-family: "FFDINWebPro", 'Helvetica Neue', Arial, Helvetica, sans-serif;
  letter-spacing: .05em;
}
.challenge-point {
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  border-radius: 50%;
  background-color: #ffd34e;
}
.challenge-points-number {
  font-family: "FFDINWebProBold", 'Helvetica Neue', Arial, Helvetica, sans-serif;
  letter-spacing: .05em;
  color: #429ccd;
  font-size: 30px;
  line-height: 1;
}
.challenge-points-text {
  letter-spacing: .05em;
  font-family: "FFDINWebPro", 'Helvetica Neue', Arial, Helvetica, sans-serif;
  font-size: 0.85714286rem;
}
/*******************************
        KEY-VALUE LIST
*******************************/
.key-value {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  margin-top: -5px;
}
.key-value > li {
  position: relative;
  border-bottom: 1px solid #e0e0e0;
  margin-top: 10px;
  padding-bottom: 10px;
  padding-right: 30px;
}
.key-value > li:first-child {
  margin-top: 0;
}
.key-value > li.slim.field {
  margin-top: 10px;
  margin-bottom: 0;
  padding-bottom: 0;
  padding-right: 0;
  border-bottom: 0;
}
.key-value .key {
  color: #999999;
  text-transform: uppercase;
  font-size: 0.71428571rem;
  font-family: "FFDINWebPro", 'Helvetica Neue', Arial, Helvetica, sans-serif;
}
.key-value .value {
  color: #578CEE;
  font-size: 1.14285714rem;
}
.form-field .key-value .value {
  position: absolute;
}
.key-value .action {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.accounts {
  margin-top: -10px;
}
.accounts li {
  line-height: 33px;
}
.accounts a {
  display: block;
  color: #58595b;
  font-size: 1.14285714rem;
}
.accounts .icon {
  width: 20px;
  height: 20px;
  margin-right: 10px;
  position: relative;
  top: -2px;
  vertical-align: middle;
}
.ui.timeline {
  position: relative;
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1.14285714rem;
}
.ui.timeline:before {
  content: "";
  position: absolute;
  top: 0;
  left: 9px;
  display: block;
  height: 100%;
  width: 1px;
  border-right: 2px solid #e0e0e0;
}
.ui.timeline:after {
  content: "";
  display: block;
  position: absolute;
  bottom: -5px;
  left: 10px;
  height: 20px;
  width: 2px;
  background-image: linear-gradient(to bottom, transparent 0%, #f9f9f9 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#fff9f9f9', GradientType=0);
}
.ui.timeline > li {
  position: relative;
  padding-left: 30px;
  padding-bottom: 20px;
}
.timeline-point {
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #81caa5;
  border-radius: 50%;
  text-align: center;
}
.timeline-point .icon {
  width: 13px;
  height: 13px;
  fill: #FFFFFF !important;
  position: relative;
  top: -3px;
}
.timeline-meta {
  color: #999999;
  font-size: 0.85714286rem;
  font-family: "FFDINWebPro", 'Helvetica Neue', Arial, Helvetica, sans-serif;
}
.timeline-share {
  display: inline-block;
  text-transform: uppercase;
  font-size: 0.85714286rem;
  font-family: "FFDINWebPro", 'Helvetica Neue', Arial, Helvetica, sans-serif;
}
.timeline-share .icon {
  width: 16px;
  height: 16px;
  vertical-align: middle;
  margin-right: 5px;
  position: relative;
  top: -1px;
}
.timeline-share-twitter {
  color: #2cb7ff;
  margin-right: 15px;
}
.timeline-share-twitter:hover,
.timeline-share-twitter:focus {
  color: #2cb7ff;
}
.timeline-share-facebook {
  color: #3B5998;
}
.timeline-share-facebook:hover,
.timeline-share-facebook:focus {
  color: #3B5998;
}
