.fixed.cover {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.login {
  background: url("../images/child.jpg") no-repeat center center;
  background-size: cover;
}
.faded:before {
  content: "";
  display: block;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 30vh;
  z-index: 100;
  background-image: linear-gradient(to bottom, transparent 0%, #000 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#ff000000', GradientType=0);
}
/*******************************
        HELPERS
*******************************/
.light.text {
  color: #fff;
}
.quiet.text {
  color: #999999 !important;
}
.bleached.text {
  color: rgba(102, 102, 102, 0.4);
}
.delete.text {
  color: #e94d4d;
}
.overflowed.text {
  max-width: 100%;
  display: block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.middle.top.offset {
  margin-top: 21px !important;
}
.middle.bottom.offset {
  margin-bottom: 21px !important;
}
.large.bottom.offset {
  margin-bottom: 28px !important;
}
.small.top.offset {
  margin-top: 14px !important;
}
.small.bottom.offset {
  margin-bottom: 14px !important;
}
.large.text {
  font-size: 16px;
}
.small.text {
  font-size: 13px;
}
.big.text {
  font-size: 30px;
  line-height: 1;
}
.strong {
  font-weight: bold;
}
.primary.text {
  color: #429ccd;
}
.right.floated {
  float: right;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.center.aligned {
  text-align: center;
}
.delete-link {
  display: inline-block;
  color: #e94d4d;
  fill: #e94d4d;
  font-size: 16px;
}
.delete-link:hover,
.delete-link:focus {
  color: #ef7a7a;
  fill: #ef7a7a;
}
.delete-link .icon {
  width: 20px;
  height: 20px;
  position: relative;
  top: 3px;
  margin-right: 2px;
}
.avatar {
  border-radius: 50%;
  vertical-align: middle;
}
td .avatar {
  margin-right: 10px;
}
.share-box {
  max-width: 250px;
  margin: 14px auto 0;
}
.share-box .button {
  margin-bottom: 14px;
}
.rank {
  width: 45px;
  padding-right: 0 !important;
  text-align: center !important;
}
.points {
  width: 90px;
  padding-left: 0 !important;
  text-align: right !important;
}
.user {
  display: table;
  width: 100%;
  table-layout: fixed;
}
.user-avatar,
.user-name {
  display: table-cell;
  vertical-align: middle;
}
.user-avatar {
  width: 40px;
}
