/*******************************
            SIDEBAR
*******************************/
.ui.sidebar {
  width: 100%;
  max-width: 320px;
  height: 100% !important;
  padding-top: 30px;
  padding-bottom: 20px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  color: #fff;
  text-align: center;
  background: rgba(87, 140, 238, 0.25) url("../images/child-blended.jpg") no-repeat center center;
  background-size: cover;
  transform: translate3d(-100%, 0, 0);
  transition: transform 0.35s;
  backface-visibility: hidden;
  will-change: transform;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 1000;
}
.ui.sidebar.visible {
  transform: translate3d(0, 0, 0);
}
@media (min-width: 992px) {
  .ui.sidebar {
    transform: translate3d(0, 0, 0);
  }
}
.ui.sidebar .toggle-sidebar {
  position: absolute;
  top: 10px;
  left: 15px;
}
@media (min-width: 992px) {
  .ui.sidebar .toggle-sidebar {
    display: none;
  }
}
.sidebar-header {
  margin-bottom: 25px;
}
.sidebar-title {
  margin-top: 15px;
  color: rgba(255, 255, 255, 0.6);
  font-family: 'FFDINWebPro', Arial, Helvetica, sans-serif;
  font-weight: bold;
}
.ui.sidebar-menu {
  list-style: none;
  padding-left: 0;
}
.ui.sidebar-menu li {
  border-bottom: 1px solid rgba(247, 247, 247, 0.1);
}
.ui.sidebar-menu li:first-child {
  border-top: 1px solid rgba(247, 247, 247, 0.1);
}
.ui.sidebar-menu a {
  display: block;
  padding-top: 15px;
  padding-bottom: 15px;
  color: #fff;
  text-transform: uppercase;
  font-size: 12px;
  font-family: "FFDINWebProBold", 'Helvetica Neue', Arial, Helvetica, sans-serif;
  letter-spacing: .1em;
}
.ui.sidebar-menu a:hover,
.ui.sidebar-menu a:focus {
  background-color: rgba(255, 255, 255, 0.1);
}
.sidebar-cta {
  margin-top: 35px;
  margin-right: 10px;
  margin-left: 10px;
}
