.card {
  margin-left: 1px;
  margin-right: 1px;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 0.21428571rem;
  transform: scale(0.95);
  background-color: #f7f7f7;
}
.slick-current .card {
  transform: scale(1);
}
@media (min-width: 700px) {
  .card {
    background-color: #fafbfb;
  }
}
.card-header {
  padding-top: 21px;
  padding-bottom: 10px;
  font-family: "FFDINWebPro", 'Helvetica Neue', Arial, Helvetica, sans-serif;
}
.card-header .icon {
  width: 35px;
  height: 35px;
}
.leaderboard-footer {
  padding-top: 21px;
  padding-bottom: 21px;
  text-align: center;
}
.leaderboard-wrapper {
  position: relative;
}
.leaderboard-wrapper:before,
.leaderboard-wrapper:after {
  content: "";
  display: block;
  position: absolute;
  height: 50px;
  left: 0;
  right: 0;
}
.leaderboard-wrapper:before {
  top: 0;
  background-image: linear-gradient(to bottom, #f9f9f9 0%, rgba(255, 255, 255, 0) 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fff9f9f9', endColorstr='#00ffffff', GradientType=0);
}
@media (min-width: 700px) {
  .leaderboard-wrapper:before {
    background-image: linear-gradient(to bottom, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
    background-repeat: repeat-x;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffffff', endColorstr='#00ffffff', GradientType=0);
  }
}
.leaderboard-wrapper:after {
  bottom: 0;
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #f9f9f9 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ffffff', endColorstr='#fff9f9f9', GradientType=0);
}
@media (min-width: 700px) {
  .leaderboard-wrapper:after {
    background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
    background-repeat: repeat-x;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ffffff', endColorstr='#ffffffff', GradientType=0);
  }
}
.card .leaderboard-wrapper:before {
  background-image: linear-gradient(to bottom, #f7f7f7 0%, rgba(255, 255, 255, 0) 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fff7f7f7', endColorstr='#00ffffff', GradientType=0);
}
@media (min-width: 700px) {
  .card .leaderboard-wrapper:before {
    background-image: linear-gradient(to bottom, #fafbfb 0%, rgba(255, 255, 255, 0) 100%);
    background-repeat: repeat-x;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fffafbfb', endColorstr='#00ffffff', GradientType=0);
  }
}
.card .leaderboard-wrapper:after {
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #f7f7f7 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ffffff', endColorstr='#fff7f7f7', GradientType=0);
}
@media (min-width: 700px) {
  .card .leaderboard-wrapper:after {
    background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #fafbfb 100%);
    background-repeat: repeat-x;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ffffff', endColorstr='#fffafbfb', GradientType=0);
  }
}
