.ui.stations-container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #578cee;
  transform: translate3d(-100%, 0, 0);
  transition: transform 0.35s;
  overflow-y: hidden;
  z-index: 1020;
  border-right: 1px solid transparent;
}
@media (min-width: 992px) {
  .ui.stations-container {
    border-right-color: rgba(30, 77, 222, 0.2);
  }
}
.ui.stations-container.visible {
  transform: translate3d(0, 0, 0);
}
.stations-show {
  position: relative;
}
.stations-show .icon {
  width: 20px;
  height: 20px;
  position: absolute;
  right: 14px;
  top: 14px;
}
.stations-header {
  display: block;
  height: 44px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding: 14px;
  text-align: center;
  border-bottom: 1px solid rgba(30, 77, 222, 0.2);
}
.stations-header .icon {
  position: absolute;
  left: 14px;
  top: 9px;
}
.stations-title {
  margin-top: 0;
  margin-bottom: 0;
  color: #FFFFFF;
  text-transform: uppercase;
  font-size: 0.85714286rem;
  letter-spacing: .1em;
}
.stations-body {
  position: absolute;
  top: 44px;
  right: 0;
  bottom: 0;
  left: 0;
  overflow-y: auto;
}
.stations-body hr {
  border: 0;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.station {
  margin: 14px;
  border-radius: 0.21428571rem;
  background-color: #FFFFFF;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.25);
}
.station > a {
  display: block;
}
.station-figure {
  position: relative;
  margin: 0;
  height: 210px;
  border-top-left-radius: 0.21428571rem;
  border-top-right-radius: 0.21428571rem;
}
.station-figure img {
  position: relative;
  z-index: 100;
}
.station-figure:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0.7;
  z-index: 50;
}
.station-figure.wasser-quiz:before {
