.ui.section .title {
  color: #FFFFFF;
  background-color: #8aaad9;
  text-transform: uppercase;
  letter-spacing: .05em;
  font-size: 0.85714286rem;
  font-family: "FFDINWebPro", 'Helvetica Neue', Arial, Helvetica, sans-serif;
  margin-top: 0;
  margin-bottom: 0;
  padding: 10px 15px;
}
.ui.section .subtitle {
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 1.14285714rem;
  text-align: center;
  font-family: 'FFDINWebPro', Arial, Helvetica, sans-serif;
}
.ui.section .body {
  padding: 20px 15px;
}
.ui.section .body.full {
  padding-left: 0;
  padding-right: 0;
}
