/*--------------
      Menu
---------------*/
.ui.menu {
  display: flex;
  margin: 1rem 0em;
  font-family: "FFDINWebProBold", 'Helvetica Neue', Arial, Helvetica, sans-serif;
  background: #FFFFFF;
  text-transform: lowercaes;
  border: 1px solid #e0e0e0;
  box-shadow: 0px 1px 2px 0 #e0e0e0;
  border-radius: 0.21428571rem;
  justify-content: center;
}
.ui.menu:after {
  content: '';
  display: block;
  height: 0px;
  clear: both;
  visibility: hidden;
}
.ui.menu:first-child {
  margin-top: 0rem;
}
.ui.menu:last-child {
  margin-bottom: 0rem;
}
/*--------------
    Sub-Menu
---------------*/
/*--------------
      Item
---------------*/
.ui.menu:not(.vertical) .item {
  display: flex;
  align-items: center;
}
.ui.menu .item {
  position: relative;
  vertical-align: middle;
  line-height: 1;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  flex: 0 0 auto;
  user-select: none;
  background: none;
  padding: 7px 1.14285714em;
  text-transform: uppercase;
  color: #999999;
  font-weight: normal;
  transition: background 0.1s ease, box-shadow 0.1s ease, color 0.1s ease;
}
.ui.menu > .item:first-child {
  border-radius: 0.21428571rem 0px 0px 0.21428571rem;
}
/* Border */
.ui.menu .item:before {
  position: absolute;
  content: '';
  top: 0%;
  right: 0px;
  height: 100%;
  width: 1px;
  background: rgba(34, 36, 38, 0.1);
}
