/* Prototype */
.ui.table {
  width: 100%;
  background: #FFFFFF;
  margin: 1em 0em 1em 0em 0;
  border: 1px solid #e0e0e0;
  box-shadow: none;
  border-radius: 0.21428571rem;
  text-align: left;
  color: #58595b;
  border-collapse: separate;
  border-spacing: 0px;
}
.ui.table:first-child {
  margin-top: 0em;
}
.ui.table:last-child {
  margin-bottom: 0em;
}
/*******************************
             Parts
*******************************/
/* Table Content */
.ui.table th,
.ui.table td {
  transition: background 0.1s ease, color 0.1s ease;
}
/* Headers */
.ui.table thead {
  box-shadow: none;
}
.ui.table thead th {
  cursor: auto;
  background: #F9FAFB;
  text-align: inherit;
  color: #58595b;
  padding: 0.92857143em 0.71428571em;
  vertical-align: inherit;
  font-style: none;
  font-weight: bold;
  text-transform: none;
  border-bottom: 1px solid rgba(34, 36, 38, 0.1);
  border-left: none;
}
.ui.table thead tr > th:first-child {
  border-left: none;
}
.ui.table thead tr:first-child > th:first-child {
  border-radius: 0.21428571rem 0em 0em 0em;
}
.ui.table thead tr:first-child > th:last-child {
  border-radius: 0em 0.21428571rem 0em 0em;
}
.ui.table thead tr:first-child > th:only-child {
  border-radius: 0.21428571rem 0.21428571rem 0em 0em;
}
/* Footer */
.ui.table tfoot {
  box-shadow: none;
}
.ui.table tfoot th {
  cursor: auto;
  border-top: 1px solid #e0e0e0;
  background: #F9FAFB;
  text-align: inherit;
  color: #58595b;
  padding: 0.71428571em 0.71428571em;
  vertical-align: middle;
  font-style: normal;
  font-weight: normal;
  text-transform: none;
}
.ui.table tfoot tr > th:first-child {
  border-left: none;
}
.ui.table tfoot tr:first-child > th:first-child {
  border-radius: 0em 0em 0em 0.21428571rem;
}
.ui.table tfoot tr:first-child > th:last-child {
  border-radius: 0em 0em 0.21428571rem 0em;
}
.ui.table tfoot tr:first-child > th:only-child {
  border-radius: 0em 0em 0.21428571rem 0.21428571rem;
}
/* Table Row */
.ui.table tr td {
  border-top: 1px solid rgba(34, 36, 38, 0.1);
}
.ui.table tr:first-child td {
  border-top: none;
}
/* Table Cells */
.ui.table td {
  padding: 0.71428571em 0.71428571em;
  text-align: inherit;
}
