/*!
 * # Sprawsm - Header
 *
 */


/*******************************
            Theme
*******************************/

@type    : 'element';
@element : 'header';

@import (multiple) '../../theme.config';


/*******************************
            Header
*******************************/

.ui.header {
  height: 250px;
  padding: 15px;
  position: relative;
  color: @color;
  background-color: @background;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;

  @media (min-width: @computerBreakpoint) {
    height: 300px;
  }

  .content {
    width: 90%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 300;
  }

  .title {
    color: #fff;
    text-transform: uppercase;
    letter-spacing: .1em;
    font-size: @large;
    margin-top: 0;
  }

  .avatar,
  .icon {
    margin-bottom: @emSize / 2;
  }

  .icon {
    width: 65px;
    height: 65px;
  }

  .toggle-sidebar {
    float: left;
    position: relative;
    z-index: 350;
    fill: @white;

    .icon {
      width: 25px;
      height: 25px;
    }

    @media (min-width: @sidebarVisibilityBreakpoint) {
      display: none;
    }
  }

  .add-pin {
    float: right;
    position: relative;
    z-index: 350;
    background: rgba(255,255,255,.3);
  }
}

// Header variations
.profile {
  .avatar {
    @media (min-width: @computerBreakpoint) {
      width: 150px;
      height: 150px;
    }
  }

  .title {
    margin-bottom: 0;
  }
}

.how-to,
.profile {
  background-image: url("../images/child-blended.jpg");
}

.wasser-quiz.header {
  background-image: url("../images/child-blended.jpg");
}

.brunnen-bohren.header {
  background-image: url("../images/drill-blended.jpg");
}

.wasser-ziehen.header {
  background-image: url("../images/well-blended.jpg");
}

.der-lauf-fur-wasser.header {
  background-image: url("../images/woman-blended.jpg");
}

// Remove background image on larger screens
.how-to,
.profile,
.wasser-quiz.header,
.brunnen-bohren.header,
.wasser-ziehen.header,
.der-lauf-fur-wasser.header {
  @media (min-width: @sidebarVisibilityBreakpoint) {
    background-image: none;
  }
}

// Header for stations pages
.stations {
  .content {
    margin-top: 15px;
  }
}

//
// Header stats
//
.stats {
  max-width: 420px;
  margin: @spacingMiddle auto 0;
  padding-left: 0;
  list-style: none;
  font-family: @pageMediumFont;
  line-height: 1.4;

  > li {
    display: inline-block;
    width: 49.5%;
    margin-left: -4px;
    vertical-align: middle;
  }
}

// Single stat column
.stat {
  width: 100%;
  display: table;
  table-layout: fixed;
}

.stat-point,
.stat-text {
  display: table-cell;
  vertical-align: middle;
}

.stat-point {
  width: 55px;
}

.stat-point-circle {
  width: 45px;
  height: 45px;
  line-height: 45px;
  color: @white;
  font-weight: bold;
  text-align: center;
  background-color: @statPointBackground;
  border-radius: 50%;
}

.stat-text {
  color: @statPointColor;
  text-align: left;
  font-size: 11px;
  letter-spacing: .05em;

  @media (min-width: 380px) {
    font-size: @tiny;
  }
}

.loadUIOverrides();
