/*******************************
            Theme
*******************************/

@type    : 'element';
@element : 'segment';

@import (multiple) '../../theme.config';

/*******************************
            Segment
*******************************/

.ui.segment {
  position: relative;
  background-color: rgba(255,255,255,0.9);
  padding: @padding;
  border-radius: @borderRadius;

  .auth & {
    margin-top: 2.1875em;
  }

  &.tab {
    padding: 0;
    background-color: transparent;
    border-radius: 0;
  }
}

