/*******************************
            Theme
*******************************/

@type    : 'element';
@element : 'container';

@import (multiple) '../../theme.config';

/*******************************
            Container
*******************************/

/* All Sizes */
.ui.container {
  display: block;
  max-width: @containerBreakpoint;
  margin-left: auto;
  margin-right: auto;

  @media (min-width: @containerBreakpoint) {
    .ui.main & {
      background-color: @white;
      border-left: 1px solid #f1f1f1;
      border-right: 1px solid #f1f1f1;
      box-shadow: 0 0 10px rgba(0,0,0,.05);
    }

    .ui.header & {
      background-color: transparent;
      border-left: 0;
      border-right: 0;
    }
  }
}

