/*******************************
             Types
*******************************/

.ui.auth.container {
  max-width: @authMaxWidth !important;
  margin: @authVerticalGutter 10px;
  color: @colorQuiet;

  @media (min-width: @authBreakpoint) {
    margin-left: auto !important;
    margin-right: auto !important;
  }
}

