/* Slim input (only underline) */

.ui.slim.input {
  padding: 7px 30px 10px 0 !important;
  line-height: 28px;
  color: @slimInputColor;
  border-width: 0 0 1px 0 !important;
  border-radius: 0 !important;
  background-color: transparent !important;

  &:focus {
    border-bottom-color: @slimInputBorder;
  }

  .error & {
    color: @formErrorColor !important;

    &:focus {
      border-color: darken(@formErrorColor, 2.5%) !important;
    }
  }
}


