/*!
 * # Sprawsm - Station
 *
 */


/*******************************
            Theme
*******************************/

@type    : 'element';
@element : 'station';

@import (multiple) '../../theme.config';


/*******************************
             Station
*******************************/

// Container
.ui.stations-container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(87,140,238,1);
  transform: translate3d(-100%,0,0);
  transition: transform @sidebarAnimationSpeed;
  overflow-y: hidden;
  z-index: 1020;
  border-right: 1px solid transparent;

  // Add right border on larger screens
  @media (min-width: @sidebarVisibilityBreakpoint) {
    border-right-color: @stationsListLayoutBorderColor;
  }

  // When visible
  &.visible {
    transform: translate3d(0,0,0);
  }
}

// Show stations link in main menu
.stations-show {
  position: relative;

  .icon {
    width: 20px;
    height: 20px;
    position: absolute;
    right: @spacingSmall;
    top: @spacingSmall;
  }
}

// Fixed header
.stations-header {
  display: block;
  height: @stationsListHeaderHeight;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding: @spacingSmall;
  text-align: center;
  border-bottom: 1px solid @stationsListLayoutBorderColor;

  .icon {
    position: absolute;
    left: @spacingSmall;
    top: 9px;
  }
}

// Title
.stations-title {
  margin-top: 0;
  margin-bottom: 0;
  color: @white;
  .uppercased();
}

// Scrollable body
.stations-body {
  position: absolute;
  top: @stationsListHeaderHeight;
  right: 0;
  bottom: 0;
  left: 0;
  overflow-y: auto;

  hr {
    border: 0;
    border-top: 1px solid rgba(255,255,255,.1);
  }
}

// Single station card
.station {
  margin: @spacingSmall;
  border-radius: @defaultBorderRadius;
  background-color: @white;
  box-shadow: 0 2px 3px rgba(0,0,0,.25);

  > a {
    display: block;
  }
}

// Station figure
.station-figure {
  position: relative;
  margin: 0;
  height: 210px;
  border-top-left-radius: @defaultBorderRadius;
  border-top-right-radius: @defaultBorderRadius;

  img {
    position: relative;
    z-index: 100;
  }

  // Place background image to pseudo element so you can apply opacity
  &:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    opacity: 0.7;
    z-index: 50;
  }

  // Background variations
  &.wasser-quiz {
    &:before {
      background-image: url("../images/child.jpg");
    }
  }

  &.brunnen-bohren {
    &:before {
      background-image: url("../images/drill.jpg");
    }
  }

  &.wasser-ziehen {
    &:before {
      background-image: url("../images/well.jpg");
    }
  }

  &.der-lauf-fur-wasser {
    &:before {
      background-image: url("../images/woman.jpg");
    }
  }
}

// Station body
.station-body {
  padding: 25px 30px 20px;
}

// Station title
.station-title {
  .uppercased();
  margin-top: 0;
  margin-bottom: 5px;
}

.loadUIOverrides();
.loadMixins();
